<template>
  <div class="agent-info">
    <h6 class="title">{{ agentInfo.companyName ?? "--" }}</h6>
    <ul class="info">
      <li class="item">
        <span class="label">姓名</span>
        <p class="value">{{ agentInfo.userName }}</p>
      </li>
      <li class="item">
        <span class="label">公司</span>
        <p class="value">{{ agentInfo.companyName }}</p>
      </li>
      <li class="item">
        <span class="label">手机</span>
        <p class="value">
          <span>{{ agentInfo.mobilePhone }}</span>
          <span @click="changeTelephone">变更</span>
        </p>
      </li>
      <li class="item">
        <span class="label">密码</span>
        <p class="value">
          <span>........</span>
          <span @click="modifyPassword">修改</span>
        </p>
      </li>
      <li class="item">
        <span class="label">地址</span>
        <p class="value">{{ agentInfo.addressAll ?? "--" }}</p>
      </li>
      <li class="item">
        <span class="label">公司代码</span>
        <p class="value">{{ agentInfo.companyCode }}</p>
      </li>
    </ul>
  </div>
</template>

<script lang="ts" setup>
defineProps<{
  agentInfo: Record<string, any> | string;
}>();
const emits = defineEmits<{
  (e: "emitPassword", value: boolean): void;
  (e: "emitTelephone", value: boolean): void;
}>();
const modifyPassword = () => {
  emits("emitPassword", true);
};
const changeTelephone = () => {
  emits("emitTelephone", true);
};
</script>

<style lang="scss" scoped>
.agent-info {
  width: 30%;
  padding: 32px;
  background: #ffffff;
  border-radius: 4px;
  margin-right: 20px;

  .title {
    font-size: 24px;
    font-weight: 500;
    color: #242934;
    line-height: 28px;
    margin-bottom: 25px;
  }

  .item {
    display: flex;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    .value,
    .label {
      font-size: 14px;
      color: #9ca6b9;
      line-height: 20px;
    }

    .label {
      flex: 0 0 68px;
    }
    .value {
      span:nth-child(1) {
        margin-right: 14px;
      }
      span:nth-child(2) {
        color: #0056ff;
        cursor: pointer;
      }
    }
  }
}
</style>
