<template>
  <div class="personal-center main-container">
    <!-- 代理商信息和账户列表 -->
    <div class="company-account">
      <!--   代理商信息   -->
      <AgentInfo
        :agentInfo="paramsData.agentInfo"
        @emitPassword="handlePassword"
        @emitTelephone="handleTelephone"
      ></AgentInfo>
      <!--   账户列表   -->
      <AccountList
        :bankAccountList="paramsData.bankAccountList"
        @emitDeleteAccount="handleDeleteAccount"
        @emitAccountDialog="handleAccountDialog"
      ></AccountList>
    </div>
    <!--  产品返佣体系  -->
    <div class="rebate">
      <h6 class="title">产品返佣体系</h6>
      <RebateList :list="paramsData.productRebateList"></RebateList>
    </div>
    <!--  添加账户  -->
    <AddModifyAccountDialog
      v-model:visible="paramsData.accountVisible"
      :modifyBankInfo="paramsData.modifyBankInfo"
    ></AddModifyAccountDialog>
    <!--  修改密码  -->
    <ModifyPasswordDialog
      v-model:visible="paramsData.passwordVisible"
    ></ModifyPasswordDialog>
    <!--  变更手机号  -->
    <ChangTelephoneNumberDialog
      v-model:visible="paramsData.telephoneVisible"
    ></ChangTelephoneNumberDialog>
  </div>
</template>
<script lang="ts">
export default {
  name: "personal-center",
};
</script>
<script lang="ts" setup>
import { useStore } from "vuex";
import { reactive, watch } from "vue";
import { ElMessage } from "element-plus";

import AgentInfo from "@components/personal-center/agent-info.vue";
import RebateList from "@components/personal-center/rebate-list.vue";
import AccountList from "@components/personal-center/account-list.vue";
import ModifyPasswordDialog from "@components/personal-center/modify-password-dialog.vue";
import AddModifyAccountDialog from "@components/personal-center/add-modify-account-dialog.vue";
import ChangTelephoneNumberDialog from "@components/personal-center/chang-telephone-number-dialog.vue";
import {
  agentInfoApi,
  deleteAccountApi,
  bankAccountListApi,
  userProxyProductApi,
} from "@api/personal-center-api";
interface ParamsDataInter {
  agentInfo: Record<string, any>;
  modifyBankInfo: Record<string, any>;
  bankAccountList: unknown[];
  accountVisible: boolean;
  passwordVisible: boolean;
  telephoneVisible: boolean;
  productRebateList: unknown[];
}
const store = useStore();
const paramsData = reactive<ParamsDataInter>({
  agentInfo: {}, // 代理商信息
  passwordVisible: false, // 修改密码弹框
  telephoneVisible: false, // 修改手机号弹框
  bankAccountList: [], // 账户列表
  accountVisible: false, // 账户弹框
  modifyBankInfo: {}, // 修改当前账户信息
  productRebateList: [], // 产品返佣体系列表
});
/*-----------代理商信息-----------*/
// 获取当前代理商信息
const getAgentInfo = async () => {
  try {
    const { data } = await agentInfoApi.getData();
    paramsData.agentInfo = agentInfoApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
// 打开修改密码弹框
const handlePassword = () => {
  paramsData.passwordVisible = true;
};
// 打开变更手机号弹框
const handleTelephone = () => {
  paramsData.telephoneVisible = true;
};
/*-----------账户列表-----------*/
// 获取绑定账户列表
const getBankAccountList = async () => {
  try {
    const { data } = await bankAccountListApi.getData();
    paramsData.bankAccountList = bankAccountListApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
// 打开添加，编辑账户弹框
const handleAccountDialog = (value) => {
  paramsData.accountVisible = true;
  paramsData.modifyBankInfo = value;
};
// 删除账户
const handleDeleteAccount = async (value) => {
  try {
    const params = {
      id: value.id,
    };
    const { data } = await deleteAccountApi.getData(params);
    if (data) {
      ElMessage.success("删除成功");
      paramsData.bankAccountList = paramsData.bankAccountList.filter(
        (item: any) => {
          return item.id !== value.id;
        }
      );
    } else {
      ElMessage.error("删除失败");
    }
  } catch (e) {
    console.log(e);
  }
};
// 产品返佣体系
const userProxyProduct = async () => {
  try {
    const { data } = await userProxyProductApi.getData();
    paramsData.productRebateList = userProxyProductApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
watch(
  () => paramsData.accountVisible,
  (value) => {
    if (!value) {
      if (store.state.personalCenter.bankInfo) {
        const bankInfo = store.state.personalCenter.bankInfo;
        if (bankInfo.submitType === "modify") {
          paramsData.bankAccountList.map((item: any) => {
            if (item.id === bankInfo.id) {
              item.account = bankInfo.account;
              item.bankName = bankInfo.bankName;
              item.ownerName = bankInfo.ownerName;
            }
            return item;
          });
        } else {
          paramsData.bankAccountList.push(store.state.personalCenter.bankInfo);
        }
        store.state.personalCenter.bankInfo = "";
      }
    }
  }
);
getBankAccountList();
userProxyProduct();
getAgentInfo();
</script>
<style lang="scss" scoped>
.company-account {
  display: flex;
}

.product-order {
  margin-top: 35px;
}

.rebate {
  background: #ffffff;
  border-radius: 4px;
  padding: 32px 24px;
  margin-top: 20px;

  .title {
    font-size: 18px;
    color: #242934;
    line-height: 1;
    margin-bottom: 28px;
  }

  :deep(.el-table th.el-table__cell) {
    padding: 15px;
    background-color: #f5f7fb;
  }
}

.table-container {
  margin-top: 16px;
}
</style>
