<template>
  <el-table :data="list" style="width: 100%" :span-method="spanMethod">
    <el-table-column
      align="center"
      prop="productName"
      :label="['ec-line'].includes(systemName) ? '系列名称' : '数企系列'"
    ></el-table-column>
    <el-table-column
      align="center"
      prop="appName"
      label="产品名称"
    ></el-table-column>
    <el-table-column
      align="center"
      prop="marketPrice"
      label="市场价"
    ></el-table-column>
    <el-table-column
      align="center"
      prop="lowLevelPrice"
      label="白金服务商"
    ></el-table-column>
    <el-table-column
      align="center"
      prop="midLevelPrice"
      label="铂金服务商"
    ></el-table-column>
    <el-table-column
      align="center"
      prop="highLevelPrice"
      label="钻石服务商"
    ></el-table-column>
    <template #empty>
      <TableEmpty empty-text="暂无记录"></TableEmpty>
    </template>
  </el-table>
</template>

<script lang="ts" setup>
import { systemName } from "@assets/common";

defineProps<{
  list: Array<any>;
}>();
const spanMethod = ({ row, columnIndex }) => {
  if (columnIndex === 0) {
    if (row.appListLength > 0) {
      return {
        rowspan: row.appListLength,
        colspan: 1,
      };
    } else {
      return {
        rowspan: 0,
        colspan: 0,
      };
    }
  }
};
</script>
