<template>
  <el-dialog
    class="custom-dialog"
    width="450px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    :title="paramsData.dialogTitle"
    :model-value="visible"
    @close="$emit('update:visible', false)"
    @open="handleOpen"
  >
    <el-form
      class="form"
      ref="formRef"
      :model="formData"
      :rules="rules"
      label-width="80px"
    >
      <el-form-item label="银行卡号" prop="account">
        <el-input
          v-model="formData.account"
          placeholder="请输入银行卡号"
        ></el-input>
      </el-form-item>
      <el-form-item label="银行名称" prop="bankName">
        <el-input
          v-model="formData.bankName"
          placeholder="请输入银行名称"
        ></el-input>
      </el-form-item>
      <el-form-item label="持卡人" prop="ownerName">
        <el-input
          v-model="formData.ownerName"
          placeholder="请输入持卡人"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" color="#0056ff" @click="submit"
          >确定
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { useStore } from "vuex";
import { ref, reactive } from "vue";
import { addAccountApi, modifyAccountApi } from "@api/personal-center-api";
const props = defineProps<{
  visible: boolean;
  modifyBankInfo: Record<string, any> | string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();
const formRef = ref();
const store = useStore();
const rules = {
  account: [
    {
      required: true,
      message: "请输入银行卡号",
      trigger: "blur",
    },
  ],
  bankName: [
    {
      required: true,
      message: "请输入银行名称",
      trigger: "blur",
    },
  ],
  ownerName: [
    {
      required: true,
      message: "请输入持卡人",
      trigger: "blur",
    },
  ],
};
const paramsData = reactive({
  dialogTitle: "添加账户",
  submitFlag: false, // 节流阀
});
const formData = reactive({
  account: "",
  bankName: "",
  ownerName: "",
});
const handleOpen = () => {
  clearFormData();
  const modifyBankInfo: any = props.modifyBankInfo;
  if (modifyBankInfo.type === "add") {
    paramsData.dialogTitle = "添加账户";
  } else {
    paramsData.dialogTitle = "修改账户";
    formData.account = modifyBankInfo.value.account;
    formData.bankName = modifyBankInfo.value.bankName;
    formData.ownerName = modifyBankInfo.value.ownerName;
  }
};
const handleClose = () => {
  emits("update:visible", false);
  formRef.value.clearValidate();
};
const submit = () => {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      if (paramsData.submitFlag) return;
      paramsData.submitFlag = true;
      let params = formData;
      const modifyBankInfo: any = props.modifyBankInfo;
      if (modifyBankInfo.type === "modify") {
        params = Object.assign(params, { id: modifyBankInfo.value.id });
      }
      accountApi(modifyBankInfo.type, params)
        .then((res) => {
          paramsData.submitFlag = false;
          // 将新添加银行卡信息保存在vuex中,避免刷新列表
          let data: any = "";
          if (modifyBankInfo.type === "add") {
            data = Object.assign(res.data, {
              submitType: modifyBankInfo.type,
            });
          } else {
            data = Object.assign(params, {
              submitType: modifyBankInfo.type,
            });
          }
          store.state.personalCenter.bankInfo = data;
          // 关闭弹框
          emits("update:visible", false);
        })
        .catch(() => {
          paramsData.submitFlag = false;
        });
    } else {
      return false;
    }
  });
};
const accountApi = (type, params) => {
  if (type === "modify") {
    return modifyAccountApi.getData(params);
  } else {
    return addAccountApi.getData(params);
  }
};
const clearFormData = () => {
  formData.account = "";
  formData.bankName = "";
  formData.ownerName = "";
};
</script>

<style lang="scss" scoped>
:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
</style>
