<template>
  <div class="account-list">
    <div class="title">
      <span>我的账户</span>
      <span @click="addModifyAccount('add', '')">添加账户</span>
    </div>
    <ul class="list" v-if="bankAccountList.length">
      <li class="item" v-for="item in bankAccountList" :key="item.id">
        <div class="bank">
          <span>{{ item.bankName }}</span>
          <el-dropdown @command="handleCommand" size="small" trigger="click">
            <svg class="icon icon-more-operation" aria-hidden="true">
              <use xlink:href="#icon-more-operation"></use>
            </svg>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item :command="`delete-bd-${JSON.stringify(item)}`"
                  >删除</el-dropdown-item
                >
                <el-dropdown-item :command="`edit-bd-${JSON.stringify(item)}`"
                  >修改</el-dropdown-item
                >
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
        <div class="name-account">
          <p class="name">{{ item.ownerName }}</p>
          <p class="account">{{ item.account }}</p>
        </div>
      </li>
    </ul>
    <TableEmpty
      v-else
      elHeight="140px"
      empty-text="您还没有添加账户，试试点击右上角去添加"
    ></TableEmpty>
  </div>
</template>

<script lang="ts" setup>
defineProps<{
  bankAccountList: Array<any>;
}>();
const emits = defineEmits<{
  (e: "emitAccountDialog", value: any): void;
  (e: "emitDeleteAccount", value: any): void;
}>();
const handleCommand = (value) => {
  const curBankInfo: any = JSON.parse(value.split("-bd-")[1]);
  if (/^delete/.test(value)) {
    deleteAccount(curBankInfo);
  } else if (/^edit/.test(value)) {
    addModifyAccount("modify", curBankInfo);
  }
};
// 添加 type = 'add', value = ''; 修改 type = modify, value = value
const addModifyAccount = (type: "add" | "modify", value) => {
  emits("emitAccountDialog", {
    type: type,
    value: value,
  });
};
// 删除银行卡
const deleteAccount = (value) => {
  emits("emitDeleteAccount", value);
};
</script>

<style lang="scss" scoped>
.account-list {
  flex-grow: 1;
  background: #ffffff;
  border-radius: 4px;
  padding: 32px;

  .title {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    color: #242934;
    line-height: 20px;
    padding: 4px 0;
    margin-bottom: 44px;

    span:nth-child(2) {
      color: #0056ff;
      font-size: 14px;
      cursor: pointer;
    }
  }

  .list {
    display: flex;
    flex-wrap: nowrap;

    .item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: calc((100% - 40px) / 3);
      height: 18vh;
      margin-right: 20px;
      padding: 25px 28px;
      background: url(~@assets/images/bank-background.png) no-repeat center;
      background-size: 100% 100%;

      &.item:last-child {
        margin-right: 0;
      }
    }

    .bank {
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      color: #ffffff;
      line-height: 18px;
      .icon-more-operation {
        cursor: pointer;
      }
    }

    .name {
      font-size: 24px;
      color: #ffffff;
      line-height: 28px;
      margin-bottom: 10px;
    }

    .account {
      font-size: 16px;
      color: #ffffff;
      line-height: 18px;
    }
  }
}
</style>
