<template>
  <el-dialog
    class="custom-dialog"
    width="450px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    :title="paramsData.dialogTitle"
    :model-value="visible"
    @close="handleClose"
  >
    <el-form
      v-show="paramsData.step === 1"
      class="form"
      ref="formSecurityRef"
      :model="formSecurityData"
      :rules="rulesSecurity"
      label-width="100px"
    >
      <el-form-item label="登录密码" prop="passWord">
        <el-input
          v-model="formSecurityData.passWord"
          type="password"
          placeholder="请输入登陆密码"
        ></el-input>
      </el-form-item>
    </el-form>
    <el-form
      v-show="paramsData.step === 2"
      class="form"
      ref="formRef"
      :model="formData"
      :rules="rules"
      label-width="100px"
    >
      <el-form-item label="手机号" prop="mobile">
        <el-input
          v-model="formData.mobile"
          placeholder="请输入新手机号"
        ></el-input>
      </el-form-item>
      <el-form-item
        label="短信验证码"
        prop="mobileCode"
        class="telephone-verification"
      >
        <el-input
          v-model="formData.mobileCode"
          placeholder="请输入短信验证码"
        ></el-input>
        <div class="telephone-code">
          <span class="count" v-if="!paramsData.telephoneCodeObj.flag"
            >{{ paramsData.telephoneCodeObj.count }}s</span
          >
          <span class="word" v-else @click="getTelephoneCode">获取验证码</span>
        </div>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button
          v-show="paramsData.step === 1"
          type="primary"
          @click="nextClick"
          >下一步</el-button
        >
        <el-button v-show="paramsData.step === 2" @click="handleClose"
          >取消</el-button
        >
        <el-button v-show="paramsData.step === 2" type="primary" @click="submit"
          >确定</el-button
        >
      </div>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, reactive } from "vue";
import { ElMessage } from "element-plus";
import {
  loginApi,
  sendSmsApi,
  testSmsApi,
  updateMobileApi,
  changeMobileApi,
} from "@api/login-register-api";
import { testTelephone, LocalStorage } from "jz-tool-lib";
defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();

const { bd_userInfo: userInfo, bd_authorization: authorization } =
  LocalStorage.batchGetItem(["bd_userInfo", "bd_authorization"]);

const formRef = ref();
const formSecurityRef = ref();
const rules = {
  mobile: [
    {
      required: true,
      message: "请输入手机号码",
      trigger: "blur",
    },
  ],
  telephoneCode: [
    {
      required: true,
      message: "请输入短信验证码",
      trigger: "blur",
    },
  ],
};
const rulesSecurity = {
  passWord: [
    {
      required: true,
      message: "请输入登陆密码",
      trigger: "blur",
    },
  ],
};
const formData = reactive({
  mobile: "",
  mobileCode: "",
});
const formSecurityData = reactive({
  passWord: "",
});
const paramsData = reactive({
  step: 1,
  submitFlag: false,
  dialogTitle: "安全验证",
  telephoneCodeObj: {
    flag: true,
    timer: 0,
    count: 60,
  },
  telephoneCodeFlag: false,
});
const handleClose = () => {
  paramsData.step = 1;
  formRef.value.resetFields();
  formSecurityRef.value.resetFields();
  emits("update:visible", false);
};
const nextClick = () => {
  let params = {
    userName: userInfo.mobile,
    passWord: formSecurityData.passWord,
    auto: 0,
    terminaltype: "WEB",
  };
  formSecurityRef.value.validate((valid: boolean) => {
    if (valid) {
      loginApi.getData(params).then((res) => {
        const data = res.data;
        LocalStorage.setItem("bd_authorization", data.auth.accessToken);
        paramsData.step = 2;
        paramsData.dialogTitle = "修改手机号";
      });
    } else {
      return false;
    }
  });
};
// 获取短信验证码
const getTelephoneCode = () => {
  if (paramsData.telephoneCodeFlag) return;
  paramsData.telephoneCodeFlag = true;
  if (!formData.mobile) {
    paramsData.telephoneCodeFlag = false;
    ElMessage.error("请输入手机号");
    return;
  }
  if (!testTelephone(formData.mobile)) {
    paramsData.telephoneCodeFlag = false;
    ElMessage.error("请输入正确的手机号码");
    return;
  }
  let params = {
    mobile: formData.mobile,
    token: authorization,
  };
  sendSmsApi
    .getData(params)
    .then(() => {
      ElMessage.error("短信已发送");
      paramsData.telephoneCodeFlag = false;
      telephoneCodeInterval();
    })
    .catch(() => {
      paramsData.telephoneCodeFlag = false;
    });
};
// 短信验证码倒计时
const telephoneCodeInterval = () => {
  paramsData.telephoneCodeObj.timer = setInterval(() => {
    if (paramsData.telephoneCodeObj.count === 0) {
      clearInterval(paramsData.telephoneCodeObj.timer);
      paramsData.telephoneCodeObj.flag = true;
      paramsData.telephoneCodeObj.count = 60;
    } else {
      paramsData.telephoneCodeObj.flag = false;
      paramsData.telephoneCodeObj.count -= 1;
    }
  }, 1000);
};
const submit = () => {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      if (paramsData.submitFlag) return;
      paramsData.submitFlag = true;
      let params = {
        mobile: formData.mobile,
        checkCode: formData.mobileCode,
      };
      testSmsApi.getData(params).then(() => {
        changeMobile();
      });
    } else {
      return false;
    }
  });
};
// 更换手机号
const changeMobile = () => {
  let params = {
    mobile: formData.mobile,
    token: authorization,
  };
  changeMobileApi.getData(params).then((res) => {
    if (res.data.rspCode === 1) {
      ElMessage.error(res.data.rspMsg);
    } else {
      updateMobile();
      emits("update:visible", false);
    }
  });
};
// 同步手机
const updateMobile = () => {
  updateMobileApi.getData({
    mobile: formData.mobile,
  });
};
</script>

<style lang="scss" scoped>
.form {
  height: 150px;
  .telephone-verification {
    position: relative;

    .el-input__inner {
      padding-right: 130px;
    }

    .telephone-code {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 0;
      right: 0;
      width: 90px;
      height: 40px;
      font-size: 14px;
      color: #0056ff;
      cursor: pointer;

      span {
        height: 100%;
      }
    }
  }
}
:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
</style>
