<template>
  <el-dialog
    class="custom-dialog"
    width="500px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    title="修改密码"
    :model-value="visible"
    @close="handleClose"
  >
    <el-form
      class="form"
      ref="ruleFormRef"
      :model="ruleFormData"
      :rules="rules"
      label-width="80px"
    >
      <el-form-item label="旧密码" prop="oldPwd">
        <el-input
          v-model="ruleFormData.oldPwd"
          type="password"
          placeholder="请输入旧密码"
        ></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="password">
        <el-input
          v-model="ruleFormData.password"
          type="password"
          placeholder="请输入新密码"
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="rePassword">
        <el-input
          v-model="ruleFormData.rePassword"
          type="password"
          placeholder="请再次输入确认密码"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" color="#0056ff" @click="submit"
          >确定</el-button
        >
      </div>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, reactive } from "vue";
import { ElMessage } from "element-plus";

import { LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@/assets/useHook";
import { validatePassword, validateRePassword } from "@/assets/validatorHook";

import { modifyPasswordApi } from "@api/login-register-api";

defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();

const ruleFormRef = ref();
const { goPage } = useVueRouter();
const authorization = LocalStorage.getItem("bd_authorization");
const paramsData = reactive({
  submitFlag: false,
});
const ruleFormData = reactive({
  oldPwd: "",
  password: "",
  rePassword: "",
});

const rules = {
  oldPwd: [
    {
      required: true,
      message: "请输入旧密码",
      trigger: "blur",
    },
  ],
  password: [
    {
      validator: (rule, value, callback) => {
        validatePassword(
          rule,
          value,
          callback,
          ruleFormData,
          ruleFormRef,
          "rePassword"
        );
      },
      trigger: "blur",
    },
  ],
  rePassword: [
    {
      validator: (rule, value, callback) => {
        validateRePassword(rule, value, callback, ruleFormData, "password");
      },
      trigger: "blur",
    },
  ],
};

const submit = () => {
  ruleFormRef.value.validate((valid: boolean) => {
    if (valid) {
      if (paramsData.submitFlag) return;
      paramsData.submitFlag = true;
      let params = {
        oldPwd: ruleFormData.oldPwd,
        newPwd: ruleFormData.password,
        token: authorization,
      };
      modifyPasswordApi
        .getData(params)
        .then(() => {
          paramsData.submitFlag = false;
          ElMessage({
            message: "密码修改成功,请重新登录",
            type: "success",
          });
          goPage("/login");
        })
        .catch((e) => {
          console.log(e);
          paramsData.submitFlag = false;
        });
    } else {
      return false;
    }
  });
};
const handleClose = () => {
  emits("update:visible", false);
  ruleFormRef.value.resetFields();
};
</script>
<style lang="scss" scoped>
:deep(.el-form-item) {
  margin-bottom: 30px;
}
:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
</style>
